body {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background: linear-gradient(to bottom,#ffc46b,#ffd557); */
}
.container-beer {
    width: 400px;
    height: 300px;
    position: relative;
    /* background: #ffcc35; */
    background: linear-gradient(to bottom,#ffb81b,#ffca43);
    box-shadow: 0 0 29px 26px #ffb919 inset;
    border-radius: 180px;
}
.counter {
    position: absolute;
    height: 2px;
    width: 75%;
    background-color: #333;
    border-radius: 2px;
    bottom: 49px;
    left: 50%;
    transform: translate(-50%);
}
.tap {
    position: absolute;
    bottom: 51px;
    left: 30%;
    transform: translate(-30%);
}
.tap .base .pipes {
    position: relative;
    height: 200px;
}
.tap .base .pipes .pipe1 {
    position: absolute;
    background-color: #ccc;
    height: 75%;
    width: 16px;
    bottom: 0;
    border-radius: 40px 40px 0 0;
    background-image: linear-gradient(to top, #bcbcbc, #bcbcbc 8px, #ccc 12px);
}
.tap .base .pipes .pipe2 {
    position: absolute;
    background-color: #ccc;
    height: 16px;
    width: 60px;
    top: 25%;
    border-radius: 40px 0 0 40px;
}
.tap .base .pipes .pipe-base {
    position: absolute;
    bottom: 0;
    background-color: #bcbcbc;
    height: 8px;
    width: 28px;
    left: -6px;
    border-radius: 2px 2px 0 0;
}
.tap .base .spicket {
    position: absolute;
    height: 36px;
    top: 50px;
    left: 60px;
}
.tap .base .spicket::before {
    content: "";
    position: absolute;
    width: 6px;
    height: 6px;
    background-color: #bcbcbc;
    top: -10px;
    left: 5px;
}
.tap .base .spicket .spout .top {
    position: absolute;
    top: 20px;
    left: 1.5px;
    height: 14px;
    width: 14px;
    background: #ccc;
    clip-path: polygon(0 0, 100% 0, 75% 100%, 25% 100%);
    animation: 8s spoutUp infinite;
}
.tap .base .spicket .spout .bottom {
    position: absolute;
    width: 11px;
    height: 2px;
    background-color: #bcbcbc;
    bottom: 1px;
    left: 3px;
    border-radius: 2px;
    animation: 8s spoutUp infinite;
}
.tap .base .spicket .cover {
    position: absolute;
    top: -6px;
    height: 28px;
    width: 16px;
    background: #d9e022;
    border-radius: 2px;
}
.tap .base .spicket .handle {
    position: absolute;
    top: -62px;
    height: 54px;
    width: 20px;
    background: #d9e022;
    border-radius: 2px;
    left: -2px;
    clip-path: polygon(2% 0, 98% 0, 75% 100%, 25% 100%);
    transform-origin: bottom;
    animation: 8s turnOnTap infinite;
}
.tap .base .spicket .handle::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 46px;
    width: 12px;
    border-radius: 2px;
    background: #ced11f;
    clip-path: polygon(2% 0, 98% 0, 75% 100%, 25% 100%);
}
.beer-glass {
    position: absolute;
    bottom: 51px;
    left: 41%;
    animation: 8s slideIn cubic-bezier(0.04, 0.58, 0.13, 1) infinite;
}
.beer-glass .glass {
    position: relative;
    height: 200px;
}
.beer-glass .glass .main {
    opacity: 0.3;
    position: absolute;
    background-color: #bcbcbc;
    height: 80px;
    width: 50px;
    bottom: 6px;
    border-radius: 2px;
}
.beer-glass .glass .main::before {
    content: "";
    position: absolute;
    bottom: -6px;
    left: 50%;
    transform: translate(-50%);
    height: 6px;
    width: 44px;
    background-color: #bcbcbc;
}
.beer-glass .glass .main::after {
    content: "";
    position: absolute;
    bottom: 4px;
    left: 7px;
    height: 66px;
    width: 6px;
    border-radius: 2px;
    background-color: #f3f3f3;
    box-shadow: 10px 0px 0px #f3f3f3, 20px 0px 0px #f3f3f3, 30px 0px 0px #f3f3f3;
}
.beer-glass .glass .handle {
    position: absolute;
    height: 42px;
    width: 16px;
    bottom: 26px;
    border-radius: 0 4px 4px 0;
    left: 49px;
    background-image: linear-gradient(to right, #ccc, #ccc 1px, #eee 4px);
}
.beer-glass .glass .handle::after {
    content: "";
    position: absolute;
    background-color: #ffc537;
    height: 30px;
    width: 10px;
    bottom: 6px;
    border-radius: 2px;
    left: 0;
}
.beer-container {
    position: absolute;
    bottom: 51px;
    left: 46%;
}
.beer-container .beer {
    position: relative;
    height: 200px;
}
.beer-container .beer .stream {
    position: absolute;
    background-color: #ff9800;
    height: 106px;
    width: 8px;
    top: 85px;
    animation: 8s pourBeer linear infinite;
}

/*1 o 2 correct*/
.beer-container .beer .fill1 {
    position: absolute;
    bottom: 8px;
    height: 18px;
    width: 42px;
    background-color: #ffb300;
    left: -16px;
    border-radius: 0 0 2px 2px;
    animation: 8s fillCup infinite;
}


.beer-container .beer .splash1 {
    position: absolute;
    bottom: 48px;
    left: -10px;
    background-color: #ffb300;
    height: 6px;
    width: 6px;
    border-radius: 50% 50% 0 50%;
    transform-origin: bottom;
    animation: 8s splash infinite;
}
.beer-container .beer .splash1::before {
    content: "";
    position: absolute;
    bottom: 2px;
    left: 12px;
    background-color: #ff9800;
    height: 6px;
    width: 6px;
    border-radius: 50% 50% 0 50%;
    transform: rotate(45deg);
}
.beer-container .beer .splash1::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 24px;
    background-color: #ff9800;
    height: 6px;
    width: 6px;
    border-radius: 50% 50% 50% 0%;
}
.beer-container .beer .foam1 {
    position: absolute;
    bottom: 25px;
    height: 8px;
    width: 42px;
    background-color: #fff;
    z-index: 1;
    opacity: 1;
    left: -16px;
    border-radius: 2px 2px 0 0;
    animation: 8s foamItUp infinite;
}

