.divide {
    height: 300px;
    width: 400px;
    background-color: #fff;
    position: absolute;
    right: -350px;
}
@keyframes pourBeer {
    0% {
        transform-origin: top;
        transform: scaleY(0);
   }
    28% {
        transform-origin: top;
        transform: scaleY(0);
   }
    30% {
        transform-origin: top;
        transform: scaleY(1);
   }
    50% {
        transform: scaleY(1);
        transform-origin: bottom;
   }
    52% {
        transform: scaleY(0);
   }
    100% {
        transform: scaleY(0);
   }
}
@keyframes fillCup {
    0% {
        transform: scaleY(0);
        transform-origin: bottom;
   }
    30% {
        transform: scaleY(0);
        transform-origin: bottom;
   }
    75% {
        transform: scaleY(1);
        transform-origin: bottom;
   }
    100% {
        transform: scaleY(1);
        transform-origin: bottom;
   }
}
@keyframes foamItUp {
    0% {
        transform: scaleY(0);
   }
    65% {
        transform: scaleY(0);
        transform-origin: bottom;
   }
    75% {
        transform: scaleY(1);
        transform-origin: bottom;
   }
}
@keyframes turnOnTap {
    24% {
        transform: rotate(0deg);
   }
    25% {
        transform: rotate(-14deg);
   }
    49% {
        transform: rotate(-14deg);
   }
    50% {
        transform: rotate(0deg);
   }
}
@keyframes slideIn {
    0% {
        transform: translate(400px);
   }
    25% {
        transform: translate(0);
   }
}
@keyframes spoutUp {
    0% {
        transform: translateY(0px);
   }
    24% {
        transform: translateY(0px);
   }
    27% {
        transform: translateY(-4px);
   }
    30% {
        transform: translateY(0px);
   }
}
@keyframes splash {
    0% {
        opacity: 0;
        transform: translateY(30px);
   }
    52% {
        opacity: 0;
        transform: translateY(30px);
   }
    55% {
        opacity: 1;
        transform: translateY(0);
   }
    75% {
        transform: translateY(30px);
   }
    100% {
        transform: translateY(30px);
   }
}
